<template>
  <view class="mine-container" :style="{height: `${windowHeight}px`}">
    <view class="content">
      <!-- 搜索框 -->
      <!-- 	<view class="search-bar">
				<input type="text" placeholder="请输入搜索关键词" @input="onSearchInput" />
			</view> -->

      <!-- 轮播图 -->
      <!-- <swiper class="carousel" autoplay interval="3000">
        <swiper-item class="swiper-item">
          <image class="img" style="width: 100%;height: 100%;  object-fit: cover;"
            src="https://qkx.meooota.com/qkxupfile/bannerxdzx.jpg" mode="scaleToFill">
          </image>
        </swiper-item>
        <swiper-item>
          <image class="img" style="width: 100%;height: 100%;" src="https://qkx.meooota.com/qkxupfile/banner1.jpg"
            mode="scaleToFill">
          </image>
        </swiper-item>
        <swiper-item>
          <image class="img" style="width: 100%;height: 100%;" src="https://qkx.meooota.com/qkxupfile/banner2.jpg"
            mode="scaleToFill">
          </image>
        </swiper-item>
      </swiper> -->

      <!-- 3D轮播图 -->
      <swiper class="header" circular previous-margin="50rpx" next-margin="50rpx" :current="current"
        @change="changeIndex">
        <swiper-item v-for="(item, index) in bannerList" :key="index">
          <!-- 当前所在滑块的 index == index 放大 -->
          <view :class=" current === index ? 'slide-image active' : 'slide-image quiet'">
            <image :src="item.url" style="width: 100%;height: 100%;border-radius: 32rpx;">
            </image>
            <!--     <view class="" style="padding: 0 20rpx;">
              <view class=""
                style="display: flex; color: #000;font-weight: 600;height: 60rpx;align-items: center;justify-content: space-between;">
                <view class="">
                  {{item.title}}
                </view>
                <image src="https://haowu.shenzhenlinghang.com/uploads/20240924/0e234868d2cff972a73c8299b4f6d7a7.png"
                  mode="heightFix" style="height: 50rpx;"></image>
              </view>
              <view class="innnerbox">
                <view class="inner1">
                  <view class="inner2" style="width: 70%;height: 100%;background-color: #6ac4b0;border-radius: 15rpx;">

                  </view>
                </view>
                <view class="">
                  已抢{{item.count}}份
                </view>
              </view>
            </view> -->
          </view>
        </swiper-item>
      </swiper>

      <!-- Tab导航 -->
      <view class="tab-nav">
        <view class="tab-item" v-for="(item, index) in tabItems" :key="index"
          :class="{ active: selectedIndex === index }" @click="onTabItemClick(index)">
          {{ item.name }}
        </view>

      </view>
      <!-- 列表 -->

      <scroll-view v-if="listItems.length >0" scroll-y class="list" @scrolltolower="onScrollToLower" refresher-enabled
        :refresher-triggered="triggered" @refresherpulling="onPulling" @refresherrefresh="onRefresh">
        <view class="list-item" v-for="(item, index) in listItems" :key="index" @click="onListItemClick(item)">
          <view class="point-text">
            <view class="title">{{ item.title }}</view>

          </view>
          <view class="tags">
            <view class="tag" v-for="(tag, tagIndex) in item.tags" :key="tagIndex"
              :style="{ backgroundColor: tag.color }">
              {{ tag.text }}
            </view>
          </view>
          <view style="color: #8f8f94;display:flex;justify-content: space-between;" class="point-text">
            <text>{{ item.create_time }}</text>
            <text class="browse-text" style="margin-top: -0.2vh;">
              {{ item.browse_num }}人看过</text>
            <text class="resource-text" style="margin-top: -0.2vh;">{{ item.resource }}</text>
            <view class="star" @click.stop="toggleFavorite(item)">
              <text :class="{ 'star-filled': item.isCol==1, 'star-empty': item.isCol==0 }"></text>
            </View>
          </view>
        </view>
        <view v-if="!hasMore" class="end-text">
          已经到底啦~
        </view>
      </scroll-view>

      <image style="height: 100rpx;width: 100rpx;margin-top: 200rpx;padding: 20rpx;" v-if="listItems.length < 1"
        src="../static/images/no_data.png"><text v-if="listItems.length < 1" style="color: #8f8f94;">暂无数据</text></image>


    </view>
  </view>
</template>

<script>
  import storage from '@/utils/storage'
  import constant from '@/utils/constant'
  import {
    getReportList,
    changeResColStatus
  } from '@/api/report'
  export default {
    data() {
      return {
        bannerList: [{
            url: 'https://qkx.meooota.com/qkxupfile/xu01.jpg',
            count: 5000,
            title: '万益蓝品牌 折扣盛世来袭'
          },
          {
            url: 'https://qkx.meooota.com/qkxupfile/xu01.jpg',
            count: 5001,
            title: '万益蓝品牌 折扣盛世来袭'
          },
          {
            url: 'https://qkx.meooota.com/qkxupfile/xu01.jpg',
            count: 5002,
            title: '万益蓝品牌 折扣盛世来袭'
          }
        ],
        current: 0,
        triggered: false,
        searchValue: '',
        listHeight: 0,
        selectedIndex: 0, // 新增，用于跟踪当前选中的标签索引
        typeIndex: 0,
        total: '',
        hasMore: true,
        downForm: {
          keyWord: '',
          userId: storage.get(constant.userId),
          pageSize: 10,
          pageIndex: 1,
          start: '',
          end: '',
          type: '',
          order: '',
          resCode: '1002',
        },
        tabItems: [{
            name: '推荐'
          },
          {
            name: '热门'
          },
          {
            name: '订阅'
          }
        ],
        listItems: [],
      };
    },
    created() {

    },

    onShow() {
      this.$modal.loading("加载中，请耐心等待...")
      this.getListHeight();
      this.getReportList();
    },
    // 分享到微信好友
    onShareAppMessage() {
      return {
        title: '干货分享',
        path: '/pages/index',
        imageUrl: '',
      }
    },
    // 分享到朋友圈
    onShareTimeline() {
      return {
        title: '干货分享',
        path: '/pages/index',
        imageUrl: '',
      }
    },
    methods: {
      // 滑动轮播图 事件
      changeIndex(e) {
        // console.log(e.detail.current, 'e.detail.current');
        this.current = e.detail.current
      },
      getReportList() {
        getReportList(this.downForm).then(res => {
          if (res.data && res.data.list) {
            this.listItems = this.listItems.concat(res.data.list);
            this.listItems.forEach(item => {
              item.tags = item.tag.split(',').map(tagText => ({
                text: tagText,
                color: this.getRandomColor(tagText),
              }));
            });
            this.total = res.data.total;
            this.$modal.closeLoading()
          }
        })
      },

      getListHeight() {
        uni.getSystemInfo({
          success: (res) => {
            // 获取屏幕高度
            const screenHeight = res.windowHeight;
            // 根据屏幕高度计算 list 的高度，这里假设 list 高度为屏幕高度的 65%
            this.listHeight = (screenHeight * 0.72 * 2) / 3; // 将 px 转换为 rpx
            console.log("高度" + this.listHeight)
          },
        });
      },
      onPulling() {
        var that = this;
        if (!this.triggered) {
          //下拉加载，先让其变true再变false才能关闭
          this.triggered = true;
          //关闭加载状态 (转动的圈)，需要一点延时才能关闭
          setTimeout(() => {
            that.triggered = false;
          }, 1000)
        }
      },
      onRefresh() {
        this.$modal.loading("加载中，请耐心等待...")
        this.hasMore = true;
        this.downForm.pageIndex = 1;
        getReportList(this.downForm).then(res => {
          if (res.data && res.data.list) {
            this.listItems = res.data.list;
            this.listItems.forEach(item => {
              item.tags = item.tag.split(',').map(tagText => ({
                text: tagText,
                color: this.getRandomColor(tagText),
              }));
            });
            this.total = res.data.total;
            this.$modal.closeLoading()
          }
        })
      },
      onScrollToLower() {
        if (this.hasMore) {
          this.loadMore();
        }
      },
      loadMore() {
        this.$modal.loading("加载中，请耐心等待...")
        if (this.downForm.pageIndex * this.downForm.pageSize >= this.total) {
          this.$modal.closeLoading()
          this.hasMore = false;
          return;
        }
        this.downForm.pageIndex++;
        this.getReportList()
        //this.getUserPointsRecord();
      },
      onSearchInput(e) {
        this.$modal.loading("加载中，请耐心等待...")
        this.downForm.keyWord = e.detail.value;
        console.log("搜搜框" + this.searchValue)
        this.listItems = []
        this.getReportList();
      },
      onTabItemClick(index) {
        this.selectedIndex = index; // 更新选中的标签索引
        this.$modal.loading("加载中，请耐心等待...")

        if (this.tabItems[index].name == '热门') {
          this.downForm.order = 1
          this.listItems = []
          this.getReportList()
          this.$modal.closeLoading()

        } else if (this.tabItems[index].name == '推荐') {
          this.downForm.order = ''
          this.listItems = []
          this.getReportList()
          this.$modal.closeLoading()

        } else if (this.tabItems[index].name == '出海专题') {
          this.downForm.order = '3'
          this.listItems = []
          this.getReportList()
          this.$modal.closeLoading()

        } else if (this.tabItems[index].name == '订阅') {
          this.downForm.order = 2
          this.listItems = []
          this.getReportList()
          this.$modal.closeLoading()
          console.log(this.listItems.length)

        }
        console.log('点击了', this.tabItems[index].name);
      },
      onListItemClick(item) {
        uni.navigateTo({
          url: '/pages/user/detail/index?id=' + item.id
        });
        console.log('点击了列表项', item);
      },
      getRandomColor(text) {
        // 预定义的颜色集合
        const colors = ['#87CEEB', '#66CDAA', '#CD5C5C', '#DDA0DD', '#FFDAB9', '#C1CDC1', '#D1EEEE',
          '#EED2EE', '#9F79EE'
        ];
        if (text == '新鲜出炉') {
          return '#87CEEB';
        } else if (text == '深度研究') {
          return '#66CDAA';
        } else if (text == '热门') {
          return '#CD5C5C';
        } else if (text == '英文') {
          return '#DDA0DD';
        } else if (text == '行业研究') {
          return '#C1CDC1';
        } else if (text == '公司研究') {
          return '#D1EEEE';
        } else if (text == 'AI') {
          return '#EED2EE';
        } else if (text == '产业政策') {
          return '#9F79EE';
        } else {
          // 从集合中随机选择一个颜色
          return colors[Math.floor(Math.random() * colors.length)];
        }
      },
      toggleFavorite(item) {
        console.log(item.isCol)
        // 切换收藏状态
        if (this.downForm.userId != '') {
          item.userId = this.downForm.userId
          if (item.isCol == 0) {
            item.isCol = 1
            this.$modal.showToast('收藏成功')
          } else {
            item.isCol = 0
            this.$modal.showToast('取消收藏成功')
          }
          changeResColStatus(item).then(res => {

          })
          // item.isCol = !item.isCol;
        } else {
          uni.navigateTo({
            url: '/pages/login/index'
          });
        }
      },
    },
  };
</script>

<style>
  .end-text {
    text-align: center;
    padding: 20rpx;
    color: #999;
  }

  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 0;
  }

  .search-bar {
    padding: 0.5vh;
    height: 5.1vh;
    width: 95%;
    margin-top: 1vh;
    background-color: white;
    border-radius: 10rpx;
  }

  .carousel {
    margin-top: 1vh;
    height: 20vh;
    width: 95%;
  }

  .swiper-item {
    width: 95%;
    height: 100%;
  }



  .tab-nav {
    display: flex;
    margin-top: 15rpx;
    width: 95%;
    justify-content: space-around;
    padding: 10rpx 0;
    background-color: white;
    border-radius: 5rpx;
  }

  .tab-item {
    padding: 10rpx;
    position: relative;
    color: #000;
  }

  .tab-item.active {
    color: royalblue;
  }

  .tab-item.active::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 50%;
    height: 2rpx;
    background-color: blue;
  }


  .list {
    /* 根据实际情况调整高度 */
    /* 确保内容超出时可以滚动 */
    height: 64.5vh;
    width: 98%;
    margin-left: 1%;
    padding: 10rpx;

    border-radius: 5rpx;
  }

  .list-item {
    display: flex;
    flex-direction: column;
    margin-bottom: 20rpx;
    background-color: white;
    border-radius: 10rpx;
  }

  .point-text {
    padding: 30rpx;
  }

  .tags {
    display: flex;
    flex-wrap: wrap;
    margin-left: 20rpx;
  }

  .tag {
    padding: 4px 8px;
    margin: 4px;
    border-radius: 4px;
    color: white;
    font-size: 8px;
  }

  .star {
    /* 添加一些基本样式，比如位置、大小等 */
    position: relative;
    margin-top: -0.8vh;
    display: inline-block;
    z-index: 9;
  }

  .star-filled::before {
    content: '★';
    font-size: 35rpx;
    color: gold;
    /* 收藏时的颜色 */
  }

  .star-empty::before {
    content: '☆';
    font-size: 35rpx;
    color: grey;
    /* 未收藏时的颜色 */
  }

  .browse-text {
    margin-left: 60rpx;
  }

  .resource-text {
    display: inline-block;
    width: 20%;
    overflow: hidden;
    margin-left: 60rpx;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>
<style lang="scss">
  .header {
    box-sizing: border-box;
    width: 100%;
    height: 382rpx;

    // margin: 50% auto;
    // background-color: #e9f4f2;
    color: #fff;
    border-radius: 10rpx;
    // margin-bottom: 50rpx;

    .slide-image {
      width: 100%;
      // width: 600rpx;
      height: 382rpx;
      // background: #fff;
      border-radius: 10rpx;

      .innnerbox {
        margin-top: 10rpx;
        height: 20rpx;
        align-items: center;
        display: flex;
        color: #64c1ad;
        font-size: 24rpx;
        justify-content: space-between;

        .inner1 {
          width: 70%;
          background-color: #e7f6f3;
          height: 100%;
          border-radius: 15rpx;
        }
      }
    }

    .active {
      // 中间放大动画
      transform: none;
      transition: all 0.2s ease-in 0s;
    }

    .quiet {
      // 两边缩小动画
      transform: scale(0.9);
      // opacity: 0.4;
      transition: all 0.5s ease-in 0s;
    }
  }
</style>